.Notes {
  display: flex;
}

/* This is to separe the header and rest of app */
.Main {
  min-height: 140vh;
  padding: 90px 0;
}

/*  *********************************THEME************************************ */
/* ***************************************Dark********************************* */

.TextRed,
.BodyRed {
  color: #9d0913 !important;
  padding: 0;
}

.BorderRed {
  border: 1px solid #9d0913 !important;
}

.BorderRedBottom {
  border-bottom: 1px solid #9d0913;
}

.BackgroundRed,
.BodyRed {
  background-color: #100000 !important;
}

/* For the header and border */
.HeaderThemeRed {
  background-color: #240000 !important;
  border-bottom: 1px solid #410004;
}

.BackgroundRedContrast {
  background-color: #240000 !important;
  border-radius: 10px;
  border: 1px solid #410004;
  margin-top: 12px;
}

.BackgroundContainerRed,
.BackgroundContainerSmallRed {
  /* background-color: #160000 !important;
  border: 1px solid #410004; */
  text-align: center;
  margin-top: 12px;
}

.BackgroundContainer {
  text-align: center;
  margin-top: 12px;
}

.BackgroundContainerSmallRed {
  max-width: 500px;
}

.TableRed,
.TableRed > tbody > tr > td {
  border: 1px solid #9d0913 !important;
  color: #9d0913;
}

.ButtonRed,
.ButtonLinkRed {
  background-color: #240000;
  color: #9d0913;
  border: 1px solid #410004;
  font-size: 100%;
  min-width: 22vh;
}

.ButtonRed:hover,
.ButtonRed:active,
.ButtonRed:focus,
.ButtonLinkRed:hover {
  background-color: #410004 !important;
  color: #9d0913 !important;
  border: 1px solid #240000 !important;
}

.CheckBoxRed {
  background-color: #410004;
  color: #410004;
}

/* *********************************Light************************************* */

.TextTest,
.BodyTest {
  color: #000000 !important;
}

.BorderTest {
  border: 1px solid #000000 !important;
}

.BorderTestBottom {
  border-bottom: 1px solid #000000;
}

.BackgroundTest,
.BodyTest {
  background-color: #ffffff !important;
}

/* For the header and border */
.HeaderThemeTest {
  background-color: #bebebe !important;
  border-bottom: 1px solid #000000;
}

.BackgroundTestContrast {
  background-color: #bebebe !important;
  border-radius: 10px;
  border: 1px solid #000000;
  margin-top: 12px;
}

.BackgroundContainerTest {
  background-color: #bebebe !important;
  border: 1px solid #000000;
  text-align: center;
  margin-top: 12px;
}

.TableTest,
.TableTest > tbody > tr > td {
  border: 1px solid #000000 !important;
  color: #000000;
}

.ButtonTest,
.ButtonLinkTest {
  background-color: #ffffff;
  color: #000000;
  border: 2px solid #000000;
  font-size: 100%;
}

.ButtonTest:hover,
.ButtonTest:active,
.ButtonTest:focus,
.ButtonLinkTest:hover {
  background-color: #bebebe !important;
  color: #000000 !important;
  border: 2px solid #000000 !important;
}

/* BUTTONS */

.ButtonCreate {
  position: fixed;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  bottom: 10%;
  right: 3%;
  font-size: 80%;
  z-index: 2;
  color: #ffffff;
  background-color: #13451f;
  border: 0.5px solid #53b46b !important;
}

.ButtonNavBar {
  height: 60px;
  width: 60px;
  /* background-color: #240000;
  color: #9d0913; */
  border: none;
  font-size: 200%;
}

/* Small screen */
@media screen and (max-width: 1024px) {
  .Main {
    padding-top: 10px;
    padding-bottom: 90px;
  }
}
